:root {
  --background: #000000;
  --foreground: #ffffff;
}
html {
  zoom: 0.8;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-pretendard);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
